/* ESTILOS PARA LA PAGINA REGISTRAR USUARIO  */
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");


.form-container {
  margin-top:  0px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
  background-color: #a7f3d0;
 
  background-image: linear-gradient(0deg, #a7f3d0 0%, #34d399 100%);
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 20px;
  border-radius: 10px;;
  background-color: #fff;
  width: 300px !important;
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Poppins", serif;
}

.form-container label{
  font-family: "Poppins", serif;
}

 .ingresar{
  border: 1px solid #059669;
  border-radius: 4;
  background-color: #059669;
  color: white;
  width: 260px;
}

.ingresar:hover {
  border: 1px solid #059669;
  background: #059669; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #0e7490,
    #017752
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #0e7490,
    #037953
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.forgot-btn {
  background-color: #434343a4 !important;
  border: 0 !important;
}

.forgot-btn:hover {
  color: white !important;
}

.contenido{
  background-color: #a7f3d0;
  
  justify-content: center;
}

.imagen{
  background-color: #a7f3d0; 
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-control{
  border: 1px solid #017752  
}

.form-control:focus{
  border: 2px solid #017752;  
  outline: 0px none #009b6a !important;
  box-shadow: 0 0 20px #017752;
}

.container {
  max-width: 100%;
}


@media screen and (max-width: 412px) {
  .imagen{
   display: none;
  } 
   
  
  }

@media screen and (max-width: 375px) {
  .imagen{
   display: none;
  } 
   
  
  }

  @media screen and (max-width: 414px) {
    .imagen{
     display: none;
    } 
    element.style {
      min-height: 100vh;
  }
    
    }

@media screen and (max-width: 220px) {
  .imagen{
   display: none;
  } 
  .form-container form {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    width: 269px !important;
}
  
  }

@media screen and (max-width: 280px) {
  .imagen{
   display: none;
  } 
   
  
  }

  .col-9 {
    flex: 0 0 auto;
    width: 100%;
}