@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");
 
* {
  margin: 0px;
  padding: 0px; 
  box-sizing: border-box;
}
html {
  height: 100%;
  overflow-x: hidden;
   
}

body{
  width: 100%;
  min-height: 100vh;
  
  overflow-x: hidden;
  overflow-y: scroll;
}



.braintree-large-button > span:before{
  display: block !important;
  content: "Elige otro metodo de pago" !important;
}

/*BARRA DE NAVEGACIÓN */
.navbar {
  font-family: "roboto", sans-serif;
  font-size: 15px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 2px solid #059669;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
}

.search-form {
  margin-right: 10px;
  margin-top: 4px;
}
.search-form input {
  border: none;
  border-radius: 0;
}
.search-form button {
  background-color: #006d5b;
  border-radius: 0;
  color: white;
}
 
/* PÁGINA NO ENCONTRADA*/
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: #ffff;
  border: 1px solid #059669;
  background: #059669; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #0e7490,
    #017752
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #0e7490,
    #037953
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
/* ========================================= */
/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}
