====dashboard
=================== */

.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background-color: #005463 !important;
  color: white;
  padding: 20px 0px;
}
.list-group-item.active{
  background-color: #059669 !important;
  border: 1px solid #059669 !important; 

}


.dropdown-menu.active{
    background-color: #059669 !important;
    border: 1px solid #059669 !important; 
  
  }
 

.nav-item dropdown.active{
    background-color: #059669 !important;
    border: 1px solid #059669 !important; 
  
  }

.updateProduct{
  margin-top: 6rem !important;
}  

.resultPage{
  margin-top: 99px;
}


.verDetalles{
  color: #ffff;
  border: 1px solid #05397c;
  background: #05397c; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #05397c,
    #055a7c
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #05397c,
    #055a7c
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  width: 100%;
}

.form-select{
  border: 2px solid #005463 !important;
}

.form-select option:hover{
  background-color: #006d5b !important;
}

.inputQuantity{
  border: 2px solid #005463 !important;
}

.form-select::focus{
  
  
  box-shadow: none;
}

.whatsapp{
  position:fixed;
  width:60px;
  height:60px;
  bottom:50px;
  right:50px;
  background-color:#006d5b;
  color:#FFF;
  border-radius:30px;
  text-align:center;
  font-size:30px;
  z-index:100;
}

.form-control{
  border: 1px solid #017752  
}

.form-control:focus{
  border: 2px solid #017752;  
  outline: 0px none #009b6a !important;
  box-shadow: 0 0 22px #017752;
}

.select-control{
  border: 1px solid #017752  
}

.select-control:focus{
  border: 2px solid #017752;  
  outline: 0px none #009b6a !important;
  box-shadow: 0 0 10px #017752;
}

.dropdown-item:hover {
  color: var(--bs-dropdown-link-active-color);
  text-decoration: none;
  background-color: #017752 !important;
}

.dropdown-item.active {
  color: var(--bs-dropdown-link-active-color);
  text-decoration: none;
  background-color: #017752 !important;
}

.ant-modal-close{
  margin-left: 63px !important;
  display: flex !important;
  justify-content: end !important;
 
}

.ant-select-arrow{
  display: none !important;
}

.status-select{
  border: 2px solid #005463 !important  ;
  border-radius: 8px;
}

.iconHeader{
  border: none  !important;
  background-color: transparent !important;

}
 
.navbar-nav .dropdown-menu{
  width: 247px;
    font-size: 12px;
}

.userDropDown{
  width: 100px !important;
  font-size: 12px;
}

 
